<template>
	<div id="listEnter">
	    <div class="banner">
	      <img src="/static/img/banner_1.png">
	    </div>
	    <div class="title">
				<div class="title1000">
						{{title}}
				</div>
	    </div>
	    <div class="content">
	      <!--<div class="row" v-html="html"></div>-->
	      <p>
	      	2017 One Show中华青年创新竞赛第一季命题评审会于6月24-25日在北京On
	      	e Show中国办公室举办。One Show中华青年创新竞赛评审提名及评选规则严格
	      	遵循国际评审原则。今年首季评审构成除了来自大中华区知名创意机构的资深行
	      	业人士之外，也不乏许多拥有年轻化营销经验创意热店的创始人及创意总监。
	      </p>
	      <p>
	      	本赛季共收到来自大中华区近400所高校和机构的超过1874件为百事可乐和吉祥航
	      	空创作的脑洞大开的创意作品。经评选第一季共有63组优秀创意作品入围，入围选手近200人。
	      </p>
	      <p>
	      	恭喜所有成功入围的创意青年！希望率先拿到决赛入场券的你们在第二赛季再
	      	接再厉！你们不仅可以参加2017 One Show中华青年创新营（注册通道10月开
	      	放），还能参与到11月2日至8日于上海1933老场坊举办的One Show中华创意
	      	节中。聆听国际创意大师的主题演讲，出席One Show中华创意奖颁奖典礼之夜
	      	，与行业最顶尖的大咖一起交流，见证行业最顶尖的创意奖项的诞生。
	      </p>
				<br>
				<img src="/static/img/201610261.png" alt="" width="100%">
				<table  style="text-align:center" border="1" cellpadding="0" cellspacing="0" v-if="tableShow">
					<colgroup>
						<col width="8%"><col width="12%"><col width="20%"><col><col width="20%"><col width="20%"><col width="19%">
					</colgroup>
					<tbody>
						<tr>
							<td>序号</td><td>参赛编号</td><td>作品标题</td><td>作者</td><td>学校/公司</td><td>指导教师</td>
						</tr>
						<tr v-for="(tr,i) in trlist" key="i" >
							<td>{{i+1}}</td><td>{{tr.bianhao}}</td>
							<td>{{tr.title}}</td>
							<td>
								<p v-for="td in tr.mingdan">{{td.name}}</p>
							</td>
							<td>{{tr.school}}</td>
							<td><p v-for="laoshi in tr.laoshi">{{laoshi.name}}</p></td>
						</tr>
					</tbody>
				</table>
	      <a href="http://www.creativer.cn/index.php?type=1" style="display: block;width: 100%;"><img style="width: 100%;padding: 20px 0;" src="/static/img/list_enter.png" alt="" /></a>
	      <p>
	      	我们很遗憾的宣布，经实名举报及调查确认：<br>
			作品2357【百事星球，事事可乐】抄袭国外设计师在ING Creatives Festival的投稿作品；<br>
			作品3161【旅程如家】与malaysia航空公司的作品构图、元素及创意点雷同。<br>
			经组委会评审再次裁决，取消以上两件作品的入围资格。One Show中华青年创新竞赛致力于鼓励年轻创意人的创作和发展，希望大家共同监督，支持原创！<br>
	      </p>
	      <img width="100%" src="/static/img/enterLess.png" alt="" />
	    </div>
	</div>
</template>
<script>

export default {
  name: 'listEnter',
  data() {
  	return {
      data:{
      	'id':''
      },
      title:'中华青年创新竞赛',
			tableShow:false,
			trList:[
				{

				}
			],
  	}
  },
  props: {},
  filters: {},
  created() {
			this.geiArtical()
  },
  methods: {
  	geiArtical (){
	    this.utils.ajax(this, '/app/ruwei', this.data, false).then((res) => {
				 this.trlist = res.data.ruweis
				 this.tableShow = true
	    });
    },
  },
  components: {
  }
}
</script>
<style  lang="scss">
 #listEnter{
		 .banner{
			height: auto;
			overflow: hidden;
				img{
					position: relative;
					left: 50%;
					transform: translateX(-50%);
					height:140px;
					@media screen and(max-width: 800px){
					 left: 16%;
					}
				}
		 }
    .title{
      height: 40px;
      line-height: 40px;
      font-size: 18px;
      color: #020202;
      font-weight: bolder;
      background: #d3d3d3;
      padding:0 20px;
    }
		table{
			border:1px solid #999;
			color: #777;
			font-size: 14px;
			font-family:"din","microsoft yahei"!important;
			width: 100%;
			border-collapse: collapse;
			tr{
				td{
					padding:5px;
					box-sizing: border-box;
				}
			}
			p{
				color: #777
			}
		}
		@media screen and(max-width: 1000px){
			table{
				font-size: 12px;
				p{
					font-size: 12px;
				}
			}
		}
		@media screen and(max-width: 1000px){
			.title{
				margin-top: -5px;
			}
		}
    .content{
      padding: 20px;
    }
    p{
      padding: 10px 0;
      line-height: 23px;
      font-size: 15px;
      color: #3c3c3c;
      font-family: "microsoft yahei";
      font-weight: 400;
			&:first-child{
				padding-top: 0
			}
    }
		@media screen and (min-width: 1000px){
			.title1000{
				width: 1000px;
				margin: auto;
			}
			.banner{
				height: 249px;
				img{
					height: 100%;
				}
			}
			.content{
				width: 1000px;
				margin: auto;
				padding-right: 300px;
				box-sizing: border-box;

			}
		}
 }
</style>



